<template>
    <div class="apply_wrapper">
        <a class="apply_button" v-if="!haveApply" @click="wishYouWereHere">Êàë‰πüÊÉ≥Êãç</a>
        <div class="apply_content" v-if="haveApply">
            <div class="apply_content_inner">
                <p>Ë∞¢Ë∞¢‰Ω†ÂñúÊ¨¢ÊàëÁöÑÁÖßÁâáÔºåÊàë‰πüÂæàÊÉ≥Êãç‰Ω†Âì¶„ÄÇ</p>
                <template v-if="haveLocation">
                    <p v-if="ourDistance < 200">ÊàëÂèØ‰ª•Êù•‰Ω†ÁöÑÂüéÂ∏ÇÊâæ‰Ω†ÊãçÁÖß„ÄÇ</p>
                    <p v-else>ÊàëÂèØ‰ª•Áªô‰Ω†ÊãçÁÖßÔºå‰ΩÜÊòØ‰Ω†ÂøÖÈ°ªÊù•ËãèÂ∑ûÊâæÊàë„ÄÇ</p>
                </template>
            </div>
        </div>
        <a href="mailto:hergloves@gmail.com?subject='ÊÉ≥Ë¶ÅÂíå‰Ω†ÊãçÁÖß'" class="apply_button" v-if="haveApply">ÁªôÊàëÂèëÈÇÆ‰ª∂Âêß</a>
        <div class="apply_memo" v-if="haveApply">
            <p>ËØ∑‰∏çË¶ÅÊõ¥ÊîπÈÇÆ‰ª∂Ê†áÈ¢òÔºåÂèØËÉΩ‰ºöÂΩ±ÂìçËá™Âä®ÂõûÂ§ç</p>
            <p v-if="!haveLocation">ËØ∑Âú®ÈÇÆ‰ª∂ÈáåÂëäËØâÊàëÊâÄÂú®ÁöÑÂüéÂ∏Ç</p>
        </div>
    </div>
</template>

<script>
    export default {
        name: "Apply",
        data() {
            return {
                haveApply: false,
                haveLocation: false,
                ourDistance: NaN
            }
        },
        methods: {
            wishYouWereHere() {
                let that = this
                if (!navigator.geolocation) {
                    that.haveApply = true
                    console.log('ÊÇ®ÁöÑÊµèËßàÂô®‰∏çÊîØÊåÅÂú∞ÁêÜ‰ΩçÁΩÆ')
                    return;
                }
                const myLocation = {
                    latitude: 31.330824,
                    longitude: 120.643594
                }

                function getDistance(lon1, lat1, lon2, lat2) {
                    if (typeof (Number.prototype.toRad) === "undefined") {
                        Number.prototype.toRad = function () {
                            return this * Math.PI / 180;
                        }
                    }
                    let R = 6371;
                    let dLat = (lat2 - lat1).toRad();
                    let dLon = (lon2 - lon1).toRad();
                    let a = Math.sin(dLat / 2) * Math.sin(dLat / 2) +
                        Math.cos(lat1.toRad()) * Math.cos(lat2.toRad()) *
                        Math.sin(dLon / 2) * Math.sin(dLon / 2);
                    let c = 2 * Math.atan2(Math.sqrt(a), Math.sqrt(1 - a));
                    let d = R * c;
                    that.ourDistance = d
                    that.haveApply = true
                }

                function geoSuccess(position) {
                    let lat = position.coords.latitude;
                    let lon = position.coords.longitude;
                    getDistance(lon, lat, myLocation.longitude, myLocation.latitude)
                    that.haveLocation = true
                }

                function geoError() {
                    that.haveApply = true
                    console.log('Êó†Ê≥ïËé∑ÂèñÊÇ®ÁöÑ‰ΩçÁΩÆ')
                    return;
                }
                navigator.geolocation.getCurrentPosition(geoSuccess, geoError);
            }
        }
    };
</script>


